*{
    padding: 0;
    margin: 0;
}
.header{
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid black;
    padding: 0px 20px;
}
.logo-container{
    width: 100px;
}
.logo{
    height: 100%;
    width: 100%;
}
.nav-items > ul{
    display: flex;
    list-style: none;
    gap: 10px;
}
.nav-items > ul > li
{
    font-size: 1.4rem;
}
.search{
    padding: 30px;
}
.res-container{
    display: flex;
    gap: 20px;
    padding: 30px;
}
.res-card{
    width: 200px;
    height: 300px;
    border: 1px solid black;
    padding: 5px;
}
.res-card:hover{
    box-shadow: 1px 1px 10px 10px rgba(0, 0, 0, 0.397);
    transform: scale(1.1);
}
.res-card > img{
    width: 100%;
}